import _ from 'lodash'
import * as THREE from 'three'

import {section3dObjects,setupThisRenderer as simRobotResize,poseLerp,triggerAnimation} from '../components/SimRobot/SimRobot'
import Sixi2 from '../model/Sixi2'
import {currentPose} from '../components/HistoryTimeline/HistoryTimeline'

export let currentState={}
export let prevPose
export let prevBottomExpanded
export const mainReducer=(state,[actionType,data])=>{
	currentState=reducer(state,actionType,data)
	if(currentState.isPolling==='false') currentState.isPolling=false
	if(currentState.isPolling==='true') currentState.isPolling=true
	if(prevBottomExpanded !== (state.showHistoryTimeline || state.showSequenceControl)){
		console.log('Bottom drawer changed')
		window.setTimeout(simRobotResize,0)
		prevBottomExpanded=state.showHistoryTimeline || state.showSequenceControl
	}
	if(actionType==='SET_SIM_ANGLES') currentState.historySeekPos=state.poseHistory.length

	// console.log(data,actionType,currentState)
	return currentState
}

// TODO: on historyLockAttachment, apply state
function reducer(state,actionType,data){
	let pose
	switch(actionType){
		case 'SET_STATE':
			return {...state,...data}
		case 'SET_SIM_ANGLES':
			for(let i in data){
				data[i]=THREE.Math.clamp(+data[i].toFixed(3),Sixi2[i].angles.min,Sixi2[i].angles.max)
				section3dObjects[i].rotateSection(data[i])
			}
			return {...state,simPose: data}
		case 'GO':
			// TODO: isOnline state
			// axios.put(`http://${state.IP}:7878/goto`,state.simPose)
			pose=JSON.stringify(state.simPose)
			if(prevPose!==pose){
				state.poseHistory.push(pose)
				state.historySeekPos=state.poseHistory.length
				prevPose=pose
			}
			return {...state}
		case 'HISTORY_SEEK':
			let a=Math.floor(data)
			let b=Math.ceil(data)
			// const alpha=+((data%1).toFixed(1))
			const alpha=data%1
			const c=JSON.parse(currentPose)
			a=a>=state.poseHistory.length?c:JSON.parse(state.poseHistory[a])
			b=b>=state.poseHistory.length?c:JSON.parse(state.poseHistory[b])
			const p=poseLerp(a,b,alpha)
			if(state.historyLockAttachment) p['t']=c['t']
			return {...reducer(state,'SET_SIM_ANGLES',p),historySeekPos: data}
		default: return state
	}
}
