import React,{useContext} from 'react'

import './BottomDrawer.scss'
import {MainContext} from '../../contexts/MainContext'
import HistoryTimeline from '../HistoryTimeline/HistoryTimeline'

export default function BottomDrawer(){
	const {state,dispatch}=useContext(MainContext)
	return (
		<div id="BottomDrawer">
			{(state.showHistoryTimeline || state.showSequenceControl)===false?<style>:root&#123; --bottomDrawerHeight: 46px &#125;</style>:undefined}
			<div id="taskbar" className="row spc-8 f-end">
				<div className="button outline row f-center" onClick={()=>dispatch(['SET_STATE',{showHistoryTimeline: !state.showHistoryTimeline}])}>
					<div className="fa fa-history super-center"/>
					<div className="col text-center">History Timeline</div>
				</div>
				<div className="button outline row f-center" onClick={()=>dispatch(['SET_STATE',{showSequenceControl: !state.showSequenceControl}])}>
					<div className="icon-layout_directed super-center"/>
					<div className="col text-center">Sequence Controls</div>
				</div>
				<div className="button outline row f-center" onClick={()=>dispatch(['SET_STATE',{showLibrary: !state.showLibrary}])}>
					<div className="fa fa-bookmark super-center"/>
					<div className="col text-center">Library</div>
				</div>
				<div className="ml-auto h7">App Version: 0.1.0</div>
			</div>
			{state.showHistoryTimeline?<HistoryTimeline/>:undefined}
			{/*
				<PoseToImg pose={JSON.stringify({x: -00,y: -00,z: -00,u: -00,v: -00,w: -00,t: -00})}/>
				<PoseToImg pose={JSON.stringify({x: -15,y: -15,z: -15,u: -15,v: -15,w: -15,t: -15})}/>
				<PoseToImg pose={JSON.stringify({x: -30,y: -30,z: -30,u: -30,v: -30,w: -30,t: -30})}/>
			*/}
			{/*state.poseHistory.map((p,i)=><PoseToImg key={i} pose={p}/>)*/}
		</div>
	)
}
