import React,{Fragment,useState} from 'react'
import './Accordion.css'

export default function Accordion(props){
	const [expanded,setExpanded]=useState(true)

	return (
		<Fragment>
			<div
				id={props.id}
				className={(props.className || '')+' accordion'+(expanded?' expanded':'')}
				onClick={()=>setExpanded(!expanded)}>{props.head}</div>
			<div style={{maxHeight: props.maxHeight}}>{props.children}</div>
			{/*<div style={{maxHeight: expanded || '100vh'}}>{props.children}</div>*/}
		</Fragment>
	)
}
